<script>
export default {
  data() {
    return {
      title: "",
      val: 0,
      split1: 0.5,
      flag: [
        {
          a: false
        },
        {
          a: false
        },
        {
          a: false
        },
        {
          a: false
        },
        {
          a: false
        },
        {
          a: false
        },
        {
          a: false
        }
      ],
      value2: 0,
      list: [],
      sonlist: [],
      contain: [],
      book: []
    };
  },
  methods: {
    //搜索
    search() {
      if (this.title == "") {
        return;
      }
      this.$router.push({ name: "search", query: { words: this.title } });
    },
    //退出登陆
    loginout() {
      this.$store.commit("LOGOUT");
      this.$router.go(0);
    },
    //个人中心
    info() {
      if (
        this.$router.history.current.path !== "/user/info" &&
        sessionStorage.getItem("user") !== null
      ) {
        this.$router.push({ name: "user" });
      } else if (sessionStorage.getItem("user") == null) {
        this.$store.commit("LOGOUT");
        this.$router.push({ name: "login" });
      }
    },
    //去购物车
    toshop() {
      this.$router.push({ name: "shop" });
    },
    show(id) {
      this.flag[id].a = true;
    },
    none(id) {
      this.flag[id].a = false;
    }
  },
  mounted() {
    this.$axios({
      url: "http://127.0.0.1:3000/api/",
      headers: {
        authorization: "Bearer " + window.localStorage.getItem("token")
      }
    }).then(res => {
      this.list = res.data.data;
      for (let i = 0; i < this.list.length; i++) {
        this.$axios({
          method: "POST",
          url: "http://127.0.0.1:3000/api/list/" + this.list[i].id
        }).then(res => {
          this.sonlist.push(res.data.data);
        });
      }
      for (let l = 0; l < this.list.length; l++) {
        this.$axios({
          method: "POST",
          url: "http://127.0.0.1:3000/api/contain/" + this.list[l].id
        }).then(res => {
          this.contain.push(res.data.data);
        });
      }
    });
    this.$axios({
      method: "get",
      url: "http://127.0.0.1:3000/api/books"
    }).then(res => {
      this.book = res.data.data;
    });
  }
};
</script>
